import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { pickFromQueryString } from 'Shared/helpers/string'
import SignUp from 'Employer/forms/Registration/SignUp'
import { validate, asyncValidate } from 'Employer/connectors/Forms/Registration/EmployerCompleteRegistrationValidator'

export const formName = 'EmployerSignUpForm'

let Form = reduxForm({
  form: formName,
  validate,
  asyncValidate,
  shouldAsyncValidate: () => true,
  asyncBlurFields: ['email']
})(SignUp)

const mapStateToProps = state => {
  const { email } = pickFromQueryString(['email'], window.location.search)
  const decodedEmail = email ? decodeURIComponent(email) : ''

  return {
    initialValues: { email: decodedEmail }
  }
}

export default connect(mapStateToProps)(Form)



