import React from 'react'
import { Form, Field } from 'redux-form'
import { Input, Title } from 'Shared/components/FormElements/index'
import { PrimaryBlockButton } from 'Shared/components/Button/index'
import withTranslations from 'Shared/hoc/withTranslations'
import { segmentSpec } from 'TrackingScripts/scripts/employer/segment'
import { setLocalData } from 'Shared/helpers/session'

const SignUp = (props) => {
  const { error, submitting, t, handleSubmit, redirectToComplete } = props

  const submitForm = values => {
    const isEmployer = window.location.pathname.includes('employer')
    isEmployer && setLocalData('new_company_signup', isEmployer)
    segmentSpec.startSignUp(values.email)
    redirectToComplete(values)
  }

  return (
    <Form onSubmit={handleSubmit(submitForm)}>
      <Title text={t('authorization.signUp')} />
      <p className='form__text form__text--sign-up'>{t('authorization.signUpCTA')}</p>
      <Field
        component={Input}
        label={t('forms.email.label')}
        name='email'
        type='email'
        autoFocus
        placeholder={t('forms.email.placeholder')}
      />
      <PrimaryBlockButton
        className='button--submit'
        disabled={error || submitting}
        type='submit'
        block
      >
        {t('authorization.createAccount')}
      </PrimaryBlockButton>
    </Form>
  )
}

export default withTranslations(SignUp)



