import { reduxForm, getFormSyncErrors, getFormMeta } from 'redux-form'
import { connect } from 'react-redux'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import CompleteRegistration from 'Employer/forms/Registration/CompleteRegistration'
import { validate } from 'Employer/connectors/Forms/Registration/EmployerCompleteRegistrationValidator'
import { getCompanySizeAsOptions } from 'Shared/selectors/resources'
import { getIndustriesAsOptions } from 'Shared/selectors/industries'
import { fetchCompanySize } from 'Shared/ducks/resources'
import { fetchIndustries } from 'Shared/ducks/industries'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'

export const formName = 'CompleteRegistration'

let Form = reduxForm({
  form: formName,
  onSubmitFail: scrollToFirstError,
  validate
})(CompleteRegistration)

const mapStateToProps = (state, ownProps) => ({
  formErrors: getFormSyncErrors(formName)(state),
  formMeta: getFormMeta(formName)(state),
  sizeOptions: getCompanySizeAsOptions(state),
  industriesOptions: getIndustriesAsOptions(state),
  initialValues: {
    email: ownProps.email
  }
})

const mapDispatchToProps = dispatch => ({
  fetchIndustries: () => dispatch(fetchIndustries()),
  fetchCompanySize: () => dispatch(fetchCompanySize()),
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



