import React, { useEffect } from 'react'
import { Form, Field } from 'redux-form'
import Title from 'Shared/components/FormElements/Title'
import { Trans } from 'react-i18next'
import cx from 'classnames'
import { Link, withRouter } from 'react-router-dom'
import {
  InputWithPreview,
  Input,
  PhoneInput,
  CheckboxWithErrorLabel,
  Select
} from 'Shared/components/FormElements'
import { PrimaryBlockButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import './CompleteRegistration.scss'

import { isErrorInFieldFromProps } from 'Shared/helpers/forms'
import mixpanel from 'mixpanel-browser'

function CompleteRegistration (props) {
  const { error, submitting, t, handleSubmit, registerUser } = props

  useEffect(() => {
    props.fetchIndustries()
    props.fetchCompanySize()
    props.fetchCurrentLocationIfNeeded()
  }, [])

  const errorLabelClass = cx({
    'label-error__first-name-or-last-name': isErrorInFieldFromProps('lastName') || isErrorInFieldFromProps('firstName')
  })

  const trackMixpanelEvent = () => {
    mixpanel.track(`Button Click - Join Oliv - Sign Up`);
    mixpanel.track(`Button Click - Join Oliv - Employer - Sign Up`);
  };

  return (
    <Form
      className='complete-registration'
      onSubmit={handleSubmit(registerUser)}
      autocomplete='off'
    >
      <Title text={t('authorization.createYourAccount')} />
      <div className='columns form__wrapper--inline'>
        <div>
          <Field
            className='complete-registration__hidden-field'
            component={Input}
            name='email'
            type='email'
            autoComplete='username'
          />
        </div>
        <div className='column col-6'>
          <Field
            component={Input}
            label={t('forms.firstName.label')}
            name='firstName'
            labelClassName={errorLabelClass}
            placeholder={t('forms.firstName.placeholder')}
            type='text'
            autoComplete='off'
          />
        </div>
        <div className='column col-6'>
          <Field
            component={Input}
            label={t('forms.lastName.label')}
            labelClassName={errorLabelClass}
            name='lastName'
            placeholder={t('forms.lastName.placeholder')}
            type='text'
            autoComplete='off'
          />
        </div>
      </div>
      <Field
        component={Input}
        label={t('forms.companyName.label')}
        name='companyName'
        placeholder={t('forms.companyName.placeholder')}
        type='text'
        autoComplete='off'
      />
      <Field
        component={Select}
        options={props.industriesOptions}
        label={t('forms.industry.label')}
        placeholder={t('common.select')}
        name='industryId'
        withIcon
        autoComplete='off'
      />
      <Field
        component={Select}
        options={props.sizeOptions}
        label={t('forms.companySize.label')}
        placeholder={t('common.select')}
        name='size'
        withIcon
        autoComplete='off'
      />
      <Field
        component={PhoneInput}
        className={'form-group'}
        label={t('forms.phoneNumber.label')}
        name='phoneNumber'
        autoComplete='off'
      />
      <Field
        component={InputWithPreview}
        iconClassName='ion-md-lock'
        label={t('forms.password.label')}
        name='password'
        placeholder={t('forms.password.placeholder')}
        type='password'
        autoComplete='password'
      />
      <div>
        <h3 className='sign-up__subtitle'>{t('terms.privacyNotice.title')}</h3>
        <Trans i18nKey='terms.privacyNotice.description' className='sign-up__text' parent='div'>
          At Oliv, we take the privacy of our users very seriously. Our <Link to='/privacy' target='_blank'><strong>privacy
          policy</strong></Link> has been updated in accordance with the EU's General Data Protection Regulation (GDPR). Please
          read through it carefully and accept our terms only if you agree.
        </Trans>
      </div>
      <Field
        component={CheckboxWithErrorLabel}
        text={<Trans i18nKey='terms.privacyNotice.agree' parent='span'>
          I agree to the <Link to='/terms' target='_blank'><strong>terms and conditions</strong></Link>.
        </Trans>}
        name='hasAcceptedTermsAndPolicies'
      />
      <PrimaryBlockButton
        className='button--submit'
        disabled={error || submitting}
        onClick={() => {props.onClick; trackMixpanelEvent}}
        type='submit'
        block
      >
        {t('authorization.joinInternsme')}
      </PrimaryBlockButton>
    </Form>
  )
}

export default withRouter(withTranslations(CompleteRegistration))



