/* eslint-disable no-throw-literal */
import isEmail from 'validator/lib/isEmail'
import { isPhoneNumber } from 'Shared/helpers/validator'
import * as Validation from 'Shared/constants/Validation'
import { t } from 'i18next'
import OrganisationInvitesService from 'Shared/services/OrganisationInvitesService'
import * as R from 'ramda'
import { saveInviteRedirectLink } from 'Shared/ducks/user/employer'
import { makeRequiredFieldError } from 'Shared/helpers/forms'

const organisationInvitesService = new OrganisationInvitesService()

const validate = values => {
  const errors = {}
  const requiredFields = ['email', 'password', 'firstName', 'lastName', 'phoneNumber', 'companyName', 'industryId', 'size']

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = makeRequiredFieldError(t(`forms.${field}.label`))
    }
  })

  if (!values['industryId']) {
    errors['industryId'] = makeRequiredFieldError(t(`forms.industry.label`))
  }

  if (!values['size']) {
    errors['size'] = makeRequiredFieldError(t('forms.companySize.label'))
  }

  if (values.email && !isEmail(values.email)) {
    errors.email = {
      key: 'errors.invalidEmail',
      options: {}
    }
  }

  if (values.phoneNumber && !isPhoneNumber(values.phoneNumber)) {
    errors.phoneNumber = {
      key: 'errors.invalidPhoneNumber',
      options: {}
    }
  }

  if (values.password && values.password.length < Validation.PASSWORD_LENGTH) {
    errors.password = {
      key: 'errors.passwordTooShort',
      options: {
        chars: Validation.PASSWORD_LENGTH
      }
    }
  }

  if (values['hasAcceptedTermsAndPolicies'] !== true) {
    errors.hasAcceptedTermsAndPolicies = {
      key: 'errors.termsAgree'
    }
  }

  return errors
}

const asyncValidate = async (values, dispatch) => {
  if (values['email']) {
    try {
      await organisationInvitesService.verifyEmployerEmail(values.email)
    } catch (e) {
      const errorMessage = R.pathOr([], ['response', 'data', 'error', 'message'], e)
      const code = R.propOr('', ['code'], errorMessage[0])

      switch (code) {
        case 400:
          throw { email: {
            key: 'errors.emailAlreadyTaken'
          } }
        case 422:
          const inviteId = R.propOr(null, ['inviteId'], errorMessage[0])
          return dispatch(saveInviteRedirectLink(inviteId))
        case 403:
          throw { email: {
            key: 'errors.emailDomainBlackList'
          } }
        default:
          throw { email: {
            key: 'errors.emailNotProvided'
          } }
      }
    }
  }
}

export {
  validate,
  asyncValidate
}



