import React from 'react'
import { isValid } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Roles } from 'Shared/constants/User'
import CompleteRegistrationConnector from 'Employer/connectors/Forms/Registration/CompleteRegistrationConnector'
import SignUpConnector, { formName } from 'Employer/connectors/Forms/Registration/SignUpConnector'
import Registration from 'Shared/forms/Registration/index'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { registerUser } from 'Shared/ducks/user'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { removeInviteRedirectLink } from 'Shared/ducks/user/employer'

const mapStateToProps = state => ({
  role: Roles.employer.name,
  countryCode: state.geolocation.currentLocation.countryCode,
  isValid: isValid(formName)(state),
  signUp: () => <SignUpConnector />,
  completeRegistration: email => <CompleteRegistrationConnector email={email} />,
  redirectLink: state.user.inviteRedirectLink
})

const mapDispatchToProps = (dispatch, ownProps) => {
  return ({
    registerUser: async values => {
      await dispatch(registerUser(values, 'employer'))
      return ownProps.history.push(`/employer/thanks/${ownProps.location.search}`)
    },
    fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
    removeInviteRedirectLink: () => dispatch(removeInviteRedirectLink())
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(withTranslations(Registration)))



